<template>
  <div class="panorama">
    <!--<span class="demonstration">我们的爱~</span>-->
    <el-carousel trigger="click" height="400px" autoplay>
      <el-carousel-item v-for="panorama in panoramas" :key="panorama">
        <img :src="panorama" class="carousel-img" @click="open()" />
      </el-carousel-item>
    </el-carousel>

    <div class="detail">
      <el-row>
        <el-col :span="8">
          <div class="grid-content bg-purple">
            <img :src='our[0]' class="our" />
            <span class="desc">老婆漫游在油菜花的海洋里</span>
          </div>
        </el-col>
        <el-col :span="8">
          <div class="grid-content bg-purple-light">
            <img :src='our[1]' class="our" />
            <span class="desc">身后人山人海，塔寺林立</span>
            <span class="desc">我们在塔尔寺</span>
            <span class="desc">这是我们的爱情</span>
            <span class="desc">执子之手，与子偕老</span>
            <span class="desc">你许我一生陪伴</span>
            <span class="desc">我还你一世幸福</span>
          </div>
        </el-col>
        <el-col :span="8">
          <div class="grid-content bg-purple">
            <img :src='our[2]' class="our" />
            <span class="desc">老公拥抱着整个青海湖</span>
          </div>
        </el-col>
      </el-row>
    </div>

  </div>
</template>

<script>
  let panoramas = []
  for (let i = 1; i <= 6; i++) {
    panoramas.push('/static/img/panorama/' + i + '.jpg')
  }
  let our = []
  our.push('/static/img/our/girl.jpg')
  our.push('/static/img/our/we.jpg')
  our.push('/static/img/our/boy.jpg')

  export default {
    name: 'Index2',
//    components: { Desc },
    data () {
      return {
        panoramas: panoramas,
        msg: 'fjakljekjgkljkla',
        our: our
      }
    },
    methods: {
      open () {
        let content = '老婆，虽然我不会做相册，但是我会做网站啊'
        let title = '老婆：'
        this.$alert(content, title, {
          confirmButtonText: '我爱老公',
          callback: action => {
            this.$message({
              type: 'info',
              message: '老公也爱你，么么哒~'
            })
          }
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .carousel-img {
    width: 100%;
    height: 400px;
  }
  .el-carousel__panorama h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    /*line-height: 150px;*/
    margin: 0;
  }
  .el-carousel__panorama:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__panorama:nth-child(2n+1) {
    background-color: #d3dce6;
  }
  .grid-content {
    height: 500px;
  }
  /*.bg-purple {*/
    /*background: #d3dce6;*/
  /*}*/
  /*.bg-purple-light {*/
    /*background: #e5e9f2;*/
  /*}*/
  .our {
    width: 96%;
    padding-right: 2%;
    padding-top: 2px;
    padding-left: 2%;
  }
  .detail {
    background: #e5e9f2;
  }
  .desc {
    display: block;
    text-align: center;
    font-size: 20px;
    color: #13CE66;
  }
</style>



